.main-nav {
  font-family: "Oswald", "Arial", "Helvetica", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;
  position: relative;
}
.main-nav__toggle {
  display: block;
  position: absolute;
  width: 24px;
  height: 16px;
  top: -41px;
  right: 20px;
  border: none;
  cursor: pointer;
  background-color: #ffffff00;
}
.main-nav__toggle::after {
  content: "";
  position: absolute;
  width: 24px;
  height: 2px;
  background-color: #000000;
  right: 0;
  top: 0;
  box-shadow: 0 7px 0 0 #000000, 0 14px 0 0 #000000;
}
.main-nav__toggle:active::after {
  background-color: rgba(139, 139, 139, 0.3);
  box-shadow: 0 7px 0 0 rgba(139, 139, 139, 0.3), 0 14px 0 0 rgba(139, 139, 139, 0.3);
}
.main-nav--opened .main-nav__toggle::before,
.main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  right: 0;
  top: 7px;
  width: 25px;
  height: 2px;
  background-color: #000000;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
  box-shadow: none;
}
.main-nav--opened .main-nav__toggle:active::before,
.main-nav--opened .main-nav__toggle:active::after {
  background-color: rgba(139, 139, 139, 0.3);
}
.main-nav--nojs .main-nav__toggle {
  display: none;
}
.main-nav__list {
  list-style: none;
  margin: 0;
  padding: 0;
  position: absolute;
  z-index: 5;
  background-color: #ffffff;
  width: 100%;
}
.main-nav--nojs .main-nav__list {
  position: relative;
}
@media (max-width: 768px) {
  .site-list__item {
    display: none;
  }
  .main-nav--opened .site-list__item {
    display: block;
  }
}
@media (min-width: 769px) {
